}
.navbar .navSideSec ul:first-child li a {
    padding: .2rem 1.2rem;
    color: #fff;
    font-size: 1rem;
}
.navSideSec .avatar{height: 40px; width: 40px;}
.navbar .navSideSec ul li a.notification-bell{color:#fff; margin: 0 .8rem;}
