.dropdownMenu {
    position: absolute;
    background: #fff;
    width: 170px;
    right:-5px;
    border-radius: 5px;
}

.dropdownMenu:before {
    content: '';
    height: 0;
    width: 0;
    position: absolute;
    right: 16px;
    top: -10px;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 10px solid #fff;
}

.btnNavAvtar{border: 0; background: none; outline: none !important;}
