body{font-size:14px;font-family:'Poppins', sans-serif;background-color:#11161d;color:#C2C2C2;line-height:1.313rem;font-weight:400;}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5{font-weight:300;}

strong{font-weight:700;}
b{font-weight:600}

h1{font-size:86px;}
h2{font-size:35px;}
h3{font-size:24px;}
h4,.h4{font-size:18px;}
h5{font-size:16px;}
h6{font-size:15px;}

.font-9{font-size:9px;}
.font-10{font-size:10px;}
.font-12{font-size:12px;}
.sm-line-height{line-height:13.5px;}
.tp-bdr{top:10px;}
.text-peach{color:#FF755C;}
.text-pink{color:#FF196E;}
.black-bg{background-color:#0B0E12;}
.brown-bg{background-color:#212121;}
.rounded-xl{border-radius:16px;}
.peach-bg{background-color:#FF755C;}
.peach-border{border-color:#FF755C;}
.pink-bg{background-color:#FF196E;}
.pink-border{border-color:#FF196E;}
.subtext{color: #647685;}

/*Tabbings*/
.nav-pills li {
	margin-right: 20px;
}

.nav-pills li a {
	font-size: 18px;
	padding: .5rem 1.5rem;
}
.nav-pills .nav-link.active, .nav-pills .show>.nav-link{background-color:transparent;color:#FF116D!important;}
.nav-pills .nav-link.active:after, .nav-pills .show>.nav-link:after{border-bottom:7px solid #FF196E;content:'';bottom:-15px;top:0;right:0;left:0;position:absolute;}

.nav-pills.peach-bg .nav-link.active,
.nav-pills.peach-bg .show>.nav-link {
	color: #FF755C !important;
}

.nav-pills.peach-bg .nav-link.active:after,
.nav-pills.peach-bg .show>.nav-link:after {
	border-bottom: 7px solid #FF755C;
}


@media screen and (max-width:991px) {
	.container.tab .w-75{width:100%!important;}
	.w-25{width:50%!important;}
}
@media screen and (max-width:767px) {
	.container.tab{max-width:100%;}
}
@media screen and (max-width:471px) {
	.tab .nav-link{padding:12px 5px;}
	.tab .btn,.tab .h4{font-size:12px;}	
}

















