	margin: 0;
	opacity: 0; 
}
.custom-file-label {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	z-index: 1;
	height: calc(2.50rem + 2px);
	padding: .375rem .75rem;
	line-height: 2;
	background: #0B0E13;
	border-radius: 15px;
	border: 0;
	color: #fff;
	border-radius: .25rem;
}
.custom-file-label::after {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 3;
	display: block;
	height: calc(calc(2.50rem + 2px) - 1px * 2);
	padding: .375rem .75rem;
	line-height: 2;
	color: #495057;
	content: "Choose Photos";
	background-color: #e9ecef;
	border-left: 1px solid #ced4da;
	border-radius: 0 .25rem .25rem 0;
}
